const requestToAttendState = "por_atender"
const requestsObservedState = "observado"
const requestsAttendState = "atendido"


// Layers ids
const idLyrCatastroFiscal = "CARTO_FISCAL_6806"
const idLyrCfPredios = "CARTO_FISCAL_6806_0"
const idLyrCfLotes_pun = "CARTO_FISCAL_6806_1"
const idLyrCfEje_vial = "CARTO_FISCAL_6806_2"
const idLyrCfNumeracion = "CARTO_FISCAL_6806_3"
const idLyrCfArancel = "CARTO_FISCAL_6806_4"
const idLyrCfLotes = "CARTO_FISCAL_6806_5"
const idLyrCfUnidadesurbanas = "CARTO_FISCAL_6806_6"
const idLyrCfParques = "CARTO_FISCAL_6806_7"
const idLyrCfManzana = "CARTO_FISCAL_6806_8"
const idLyrCfManzanaUrb = "CARTO_FISCAL_6806_9"
