  _addPrediosNew(predio){
    let deferred = new Deferred();
    const addFeatureUrl = "https://ws.mineco.gob.pe/serverdf/rest/services/pruebas/CARTO_FISCAL/FeatureServer/0/addFeatures"

    let params = {
      features: JSON.stringify([predio]),  // Convertir el objeto de entidad a una cadena JSON
      f: "json"
    };
  
    var options = {
      url: addFeatureUrl,
      content: params,
      handleAs: "json",
      callbackParamName: "callback"
    };
  
    esriRequest(options, {
      usePost: true,
    })
    .then(function(response) {
      if (response.addResults && response.addResults.length > 0) {
        deferred.resolve(predio);
      } else {
        deferred.reject(response);
      }
    }, function(error) {
      deferred.reject(error);
    });
    return deferred.promise;
  },

  _updateStatusRequests(predioResult){
    _FIELDS_RESPONSE = [
      "COD_PRE",
      "COD_CPU",
      "COD_SECT",
      "COD_MZN",
      "COD_LOTE",
      "COD_UU",
      "COD_VIA",
      "TIPO_UU",
      "NOM_UU",
      "NOM_REF",
      "MZN_URB",
      "LOT_URB",
      "TIP_VIA",
      "NOM_VIA",
      "CUADRA",
      "LADO",
      "DIR_MUN",
      "DIR_URB",
      "COORD_X",
      "COORD_Y",
      "RAN_CPU",
      "COD_UI",
      "COD_VER"
    ]

    // la lista predioResults contiene objetos con una propiedad attributes, necesito que todos los objetos tengan solo las propiedades que necesito de acuerdo a la lista _FIELDS_RESPONSE
    let predioResult2 = predioResult.map((predio) => {
      let predio2 = {}
      _FIELDS_RESPONSE.forEach((field) => {
        predio2[field] = predio.attributes[field]
      })
      return predio2
    })




    let response = {
      "id": selfCm.codRequestsCm,
      "results": predioResult2
    }

    console.log(response)

    selfCm._sendDataToPlatform(response)
  },

